@import url(https://fonts.googleapis.com/css2?family=Fredoka+One&display=swap);
.makeStyles-root-5 {
  background-image: url("https://res.cloudinary.com/practicaldev/image/fetch/s--fZ8kJHEQ--/c_imagga_scale,f_auto,fl_progressive,h_900,q_auto,w_1600/https://dev-to-uploads.s3.amazonaws.com/uploads/articles/nphrgz8yfnjylrwfr0yl.png");
  background-position: center, center;
  background-repeat: no-repeat, no-repeat;
  padding: 25px;
  background-size: cover;
  
}

.App {
  text-align: center;
  margin: 0px;
  padding: 0%;
}

/* @import url("https://fonts.googleapis.com/css2?family=Zen+Dots&display=swap"); */

h1.MuiTypography-root {
  font-family: "Fredoka One", cursive;
}
p.MuiTypography-root {
  font-family: "Hubballi", cursive;
}

h2,
h5,
span,
h3,
button,
p {
  font-family: "Roboto", sans-serif !important;
}

@media (max-width: 670px) {
  h2,
  h5,
  span,
  h3,
  button,
  p {
    font-size: smaller;
  }
  h5.MuiTypography-root {
    font-size: 0.6rem;
  }
  h3.makeStyles-heading-17 {
    font-size: 1.5rem;
  }
  img.makeStyles-logo-4 {
    width: 70px;
    height: auto;
  }
  p.email {
    display: none;
  }
  span.makeStyles-para-18 {
    font-size: small;
  }
}

